import PropTypes from "prop-types";

import {
  Card,
  CardContent,
  Paper,
  Divider,
  Typography,
  Zoom,
} from "@material-ui/core";
import { MEALS, WEEKDAYS } from "../constants/Constants";
import { makeStyles } from "@material-ui/core/styles";

import FastfoodIcon from "@material-ui/icons/Fastfood";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import HappyIcon from "@material-ui/icons/InsertEmoticon";

import breakfast_img from "../resources/images/a.jpg";
import lunch_img from "../resources/images/lunch1.jpg";
import snacks_img from "../resources/images/snacks12.jpg";
import dinner_img from "../resources/images/dinner1.jpg";
import TodayList from "./List";

import React, { memo } from "react";

import LazyChipWrapper from "./LazyChipWrapper";
import ImageWrapper from "./ImageWrapper";
import { StyledChip, Chip } from "../base_components/StyledChip";

const images = [breakfast_img, lunch_img, snacks_img, dinner_img];

const useStyles = makeStyles((theme) => ({
  paperWrapper: {
    display: "flex",
    justifyContent: "center",
    padding: "20px",
    width: "100%",
    [theme.breakpoints.down("xs")]: {
      padding: "20px 5px",
      marginTop: "15px",
    },
  },

  card: {
    display: "flex",
    flexDirection: "row",
    height: "350px",
    width: "max(85% , 400px)",

    borderRadius: "30px 30px 30px 30px",
    [theme.breakpoints.down("sm")]: {
      width: "87%",
    },
    [theme.breakpoints.down("xs")]: {
      flexDirection: "column",
      height: "450px",
      width: "87%",
    },
  },

  cardContent: {
    flex: 5,
    paddingLeft: "5%",
    overflow: "hidden",
  },

  paperCardheading: {
    display: "flex",
    justifyContent: "space-between",
  },

  divider: { marginBottom: "10px" },
}));

const TodayCard = memo(function TodayCard({ day, index, visible }) {
  const classes = useStyles();

  console.log("todayCard", index, visible);
  return (
    <Paper className={classes.paperWrapper}>
      <Card raised elevation={12} className={classes.card}>
        <ImageWrapper
          title={MEALS[index].toUpperCase()}
          src={images[index]}
          visible={visible}
        />

        <CardContent className={classes.cardContent}>
          <Paper elevation={0} className={classes.paperCardheading}>
            <Zoom
              in={visible}
              timeout={1000}
              style={{ transitionDelay: "300ms" }}
            >
              <Typography gutterBottom variant="h5">
                {MEALS[index].toUpperCase()}
              </Typography>
            </Zoom>

            <FastfoodIcon fontSize="large" />
          </Paper>
          <Divider className={classes.divider} />

          <Zoom
            in={visible}
            timeout={1000}
            style={{ transitionDelay: "300ms" }}
          >
            <LazyChipWrapper>
              <Chip
                variant="outlined"
                clickable={true}
                color="primary"
                icon={<LocalDiningIcon />}
                label="Healthy Diet"
              />
            </LazyChipWrapper>
          </Zoom>

          <Zoom
            in={visible}
            timeout={1000}
            style={{ transitionDelay: "300ms" }}
          >
            <LazyChipWrapper>
              <StyledChip
                color="primary"
                clickable
                variant="outlined"
                icon={<HappyIcon />}
                label="Happy Meal"
              />
            </LazyChipWrapper>
          </Zoom>
          <TodayList
            type="today"
            day={day}
            meal={MEALS[index]}
            visible={visible}
          ></TodayList>
        </CardContent>
      </Card>
    </Paper>
  );
});

TodayCard.propTypes = {
  day: PropTypes.oneOf(WEEKDAYS).isRequired,
  index: PropTypes.number.isRequired,
  visible: PropTypes.bool.isRequired,
};

export default TodayCard;
