import { IconButton, Snackbar } from "@material-ui/core";
// import { Alert } from "@material-ui/lab";
import { Close } from "@material-ui/icons";
import React from "react";
import { Suspense } from "react";
import { withStyles } from "@material-ui/core/styles";
const Alert = React.lazy(() => {
  const module = import("@material-ui/lab/Alert");
  const StyledAlert = module.then((AlertModule) => {
    const StyledAlert = withStyles((theme) => ({
      root: {
        ...theme.typography.h6,
        wordBreak: "break-all",
      },
      icon: {
        ...theme.typography.h4,
        flexDirection: "column",
        justifyContent: "center",
      },
    }))(AlertModule.default);

    return { default: StyledAlert };
  });
  return StyledAlert;
});

export default function SnackBar({ open, onClose, type, message }) {
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    onClose();
  };
  return (
    <Snackbar
      style={{ maxWidth: "90%", margin: "auto" }}
      open={open}
      autoHideDuration={3000}
      onClose={handleClose}
    >
      <Suspense fallback={<div> {message}</div>}>
        <Alert
          elevation={6}
          variant="filled"
          onClose={handleClose}
          severity={type}
          action={
            <IconButton onClick={handleClose} size="medium" color="inherit">
              <Close />
            </IconButton>
          }
        >
          {message}
        </Alert>
      </Suspense>
    </Snackbar>
  );
}
