import { Add, Delete } from "@material-ui/icons";
import { memo, useContext, useEffect, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import {
  Button,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Zoom,
  Tooltip,
} from "@material-ui/core";

import empty_img from "../resources/images/empty1.svg";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import React from "react";
import AppContext from "../context/Context";

import EditableTypoGraphy from "../base_components/EditableTypography";
import { MEALS, WEEKDAYS } from "../constants/Constants";

// For Today Cards

const useListItemDeleteIconStyles = makeStyles({
  root: {
    justifyContent: "flex-end",
  },
});

// For Today CardList
const useTodayCardStyles = makeStyles((theme) => ({
  list: {
    height: "min(230px , 70%)",
    overflowY: "auto",
    width: "100% ",
    "&:empty": {
      height: "65%",
      margin: `0px auto`,
      display: "block",
      backgroundSize: "79px",
      backgroundImage: `url(${empty_img})`,
      backgroundRepeat: "no-repeat",
      backgroundPositionX: "40px",
      backgroundPositionY: "20px",
    },
    "&:empty::after": {
      content: `"No items"`,
      fontStyle: "italic",
      /* font-family: "Courier New", Courier, monospace; */
      position: "absolute",
      fontSize: "24px",
      textAlign: "center",
      /* white-space: pre; */
      top: "calc(101px + 11%)",
      left: "33px",
    },
  },

  listItem: {
    padding: "0px",
    paddingBottom: "8px",
    wordBreak: "break-all",
  },

  addButton: {
    marginBottom: "0px",
    padding: "5px",
    marginRight: "10px",
    float: "right",
    height: "fit-content",
    [theme.breakpoints.down("sm")]: {
      marginRight: "2px",
    },
  },
}));

// For WeekdayCards

const useWeekdayCardStyles = makeStyles((theme) => ({
  listItem: {
    padding: "0px",
    wordBreak: "break-all",
    whiteSpace: "normal",
  },
  list: {
    height: "52%",
    overflowY: "auto",
    width: "100%",
    paddingTop: "2px",

    "&:empty": {
      height: "180px",
      margin: `0px auto`,
      display: "block",
      backgroundSize: "79px",
      backgroundImage: `url(${empty_img})`,
      backgroundRepeat: "no-repeat",
      backgroundPositionX: "40px",
      backgroundPositionY: "20px",
    },
    "&:empty::after": {
      content: `"No items"`,
      fontStyle: "italic",
      /* font-family: "Courier New", Courier, monospace; */
      position: "absolute",
      fontSize: "24px",
      textAlign: "center",
      /* white-space: pre; */
      bottom: "28px",
      left: "30px",
    },
  },
  addButton: {
    marginBottom: "0px",
    // marginRight: "40px",
    float: "right",
    padding: "6px",
    height: "fit-content",
  },
}));

const CustomList = memo(
  function CustomList({ type = "today", data = [], updateData, visible }) {
    const todayClasses = useTodayCardStyles();
    const weekdayClasses = useWeekdayCardStyles();

    const classes = type === "today" ? todayClasses : weekdayClasses;

    const listItemDeleteIconClasses = useListItemDeleteIconStyles();

    // Ref Variable to set Transition for list item
    const isNewItemAdded = useRef(false);

    useEffect(() => {
      isNewItemAdded.current = false;
    }); // update New itme State after rendering

    function deleteItem(index) {
      if (data.length > index)
        updateData(data.slice(0, index).concat(data.slice(index + 1)));
    }

    function appendItem() {
      updateData([...data, ""]);
    }

    function updateItem(index, item) {
      if (data.length > index) {
        updateData([...data.slice(0, index), item, ...data.slice(index + 1)]);
      }
    }

    function onAddClick(e) {
      isNewItemAdded.current = true;
      appendItem();
    }

    console.log("list ", data);
    return (
      <>
        <Tooltip arrow title="Click to add new food item ">
          {type === "today" ? (
            <Button // Add button for Today Card
              className={classes.addButton}
              size="large"
              onClick={onAddClick}
              // variant="contained"
              startIcon={<Add />}
            >
              Item
            </Button>
          ) : (
            // Add Button for weekdayCards

            <IconButton
              className={classes.addButton}
              color="primary"
              size="medium"
              onClick={onAddClick}
              // variant="contained"
            >
              <Add />
            </IconButton>
          )}
        </Tooltip>
        <List className={classes.list}>
          {data.map((row, ind) => (
            <Zoom
              key={ind}
              in={visible}
              timeout={isNewItemAdded.current ? 500 : 1200}
            >
              <ListItem className={classes.listItem}>
                <ListItemIcon>
                  <LocalDiningIcon />
                </ListItemIcon>
                <ListItemText>
                  <EditableTypoGraphy
                    ind={ind}
                    data={row}
                    updateItem={updateItem}
                  />
                </ListItemText>
                <ListItemIcon classes={listItemDeleteIconClasses}>
                  <Tooltip arrow title="Click to delete the food item">
                    <IconButton
                      onClick={() => {
                        deleteItem(ind);
                      }}
                    >
                      <Delete fontSize="small" />
                    </IconButton>
                  </Tooltip>
                </ListItemIcon>
              </ListItem>
            </Zoom>
          ))}
        </List>
      </>
    );
  },
  (prev, cur) => prev.data === cur.data && prev.visible === cur.visible
);

CustomList.propTypes = {
  type: PropTypes.oneOf(["weekday", "today"]).isRequired,
  data: PropTypes.arrayOf(PropTypes.string).isRequired,
  updateData: function (props, propName, componentName) {
    const fn = props[propName];
    if (
      !fn ||
      !fn.prototype ||
      typeof fn.prototype.constructor !== "function" ||
      fn.prototype.constructor.length !== 1
    ) {
      return new Error(
        propName + ` at ${componentName} must be a function with 1 args`
      );
    }
  },
  visible: PropTypes.bool.isRequired,
};

const ListWithContext = memo(function ListWithContext({
  type = "today",
  day = "monday",
  meal = "lunch",
  visible,
}) {
  const context = useContext(AppContext);
  console.log("const ListWithContext", type, day, meal, visible);
  if (
    context &&
    context.updateData &&
    context.data &&
    context.data[day] &&
    context.data[day][meal]
  )
    return (
      <CustomList
        visible={visible}
        updateData={function updateData(items) {
          context.updateData(day, meal, items);
        }}
        type={type}
        data={context.data[day][meal]}
      ></CustomList>
    );

  return <></>;
});

ListWithContext.propTypes = {
  type: PropTypes.oneOf(["weekday", "today"]).isRequired,
  meal: PropTypes.oneOf(MEALS).isRequired,
  day: PropTypes.oneOf(WEEKDAYS).isRequired,
  visible: PropTypes.bool.isRequired,
};

export default ListWithContext;
