import { Typography } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { memo } from "react";
import PropTypes from "prop-types";
// Convert User Input to sentenceCase
function getSentenceCase(data) {
  return data
    .replace("\n", " ")
    .trim()
    .split(" ")
    .filter((x) => x.trim().length >= 1)
    .map(function (a) {
      return a[0].toUpperCase() + a.substring(1);
    })
    .join(" ");
}

const EditableTypoGraphy = function EditableTypoGraphy({
  classes,
  ind,
  data,
  updateItem,
}) {
  return (
    <Typography
      onKeyPress={(e) => {
        if (e.code === "Enter") {
          // On Enter Press Avoid Defaults
          e.preventDefault();
          e.target.blur();
          updateItem(ind, getSentenceCase(e.target.innerText));
        }
      }}
      contentEditable={true}
      suppressContentEditableWarning={true}
      onBlur={(e) => {
        if (e.target.innerText !== data)
          updateItem(ind, getSentenceCase(e.target.innerText));
      }}
      variant="body1"
      className={classes.root}
    >
      {data}
    </Typography>
  );
};

EditableTypoGraphy.propTypes = {
  classes: PropTypes.object.isRequired,
  ind: PropTypes.number.isRequired,
  data: PropTypes.string.isRequired,
  updateItem: function (props, propName, componentName) {
    const fn = props[propName];
    if (
      !fn ||
      !fn.prototype ||
      typeof fn.prototype.constructor !== "function" ||
      fn.prototype.constructor.length !== 2
    ) {
      return new Error(
        propName + ` at ${componentName} must be a function with 1 args`
      );
    }
  },
};

const EditableTypoGraphyMemo = memo(
  withStyles((theme) => ({
    root: {
      "&:empty::after": {
        // relate to font size!!
        fontStyle: "italic",
        fontFamily: "Courier New,Courier,monospace",
        content: `"Click to Edit"`,
      },
    },
  }))(EditableTypoGraphy),
  (prev, cur) => prev.data === cur.data && prev.ind === cur.ind
);

EditableTypoGraphyMemo.propTypes = {
  ind: PropTypes.number.isRequired,
  data: PropTypes.string.isRequired,
  updateItem: function (props, propName, componentName) {
    const fn = props[propName];
    if (
      !fn ||
      !fn.prototype ||
      typeof fn.prototype.constructor !== "function" ||
      fn.prototype.constructor.length !== 2
    ) {
      return new Error(
        propName + ` at ${componentName} must be a function with 1 args`
      );
    }
  },
};

export default EditableTypoGraphyMemo;
